*{margin:0;padding:0;box-sizing:border-box}.headerButtons{min-width:365px;width:100%;height:100%;background-color:#fdfeff;border-radius:15px;border:2px solid #007cbc;display:flex;align-items:center;justify-content:flex-start;font-family:"Inter",sans-serif;font-weight:500;font-size:20px;color:#282828;text-decoration:none;padding:20px}.headerButtons::after{content:"→";color:currentColor;padding-left:10px}.headerButtons:hover{color:#007cbc}.headerButtons:active{border-color:#2e379e;color:#2e379e}.headerButtons.firstSelected{color:#007cbc}.headerButtons.firstSelected:active{color:#2e379e}@media screen and (max-width: 1330px){.headerButtons{min-width:auto;width:350px}}@media screen and (max-width: 570px){.headerButtons{display:none}}