@import "../../style.module";

.header {
  margin-top: $indent-65;
  @include flex-column;
  gap: $indent-35;
  margin-bottom: $indent-35;

  &__wrapper {
    width: 100%;
    @include flex-row;
    justify-content: space-between;

    &__btns {
      @include flex-row;
      gap: $indent-20;

      &__siteLink {
        @include main-button;
        width: max-content;
        color: $provider-page-color;
        border-color: $provider-page-color;
        padding: 10px 15px;

        &:hover {
          @include main-button-hover;
          background-color: $provider-page-color;
          cursor: pointer;
        }
      }

      &__docLink {
        @include flex-row;
        @include main-button;
        width: max-content;
        color: $provider-page-color;
        border-color: $provider-page-color;
        padding: 10px 15px;

        &:hover {
          @include main-button-hover;
          background-color: $provider-page-color;
        }

        &__wrapper {
          @include flex-row;
          gap: $indent-10;

          &__p {

          }
        }
      }
    }
  }

  &__title {
    @include inter-medium-50;
    align-self: flex-start;
    color: $main-black;
  }
}

@media screen and (max-width: 895px) {
  .header__wrapper {
    flex-direction: column;
    align-items: flex-start;
    gap: $indent-20;
  }
}

@media screen and (max-width: 570px) {
  .header__wrapper__btns {
    flex-direction: column;
    align-items: flex-start;
    gap: $indent-10;
  }

}

@media screen and (max-width: 485px) {
  .header__title {
    @include inter-medium-35;
    font-size: 30px;
  }

  .header__wrapper__btns__siteLink, .header__wrapper__btns__docLink {
    @include inter-regular-18;
  }
}

@media screen and (max-width: 405px) {
  .header {
    margin-top: $indent-35;
  }
}